.changelog {
  border-radius: 10px;
  box-shadow: inset 0px 0px 0px 2px #aaffaf;
  max-height: 600px;
  overflow-y: scroll;
  position: relative;
  &__header {
    background-color:  #1f731b;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    color: #fff;
    text-align: center;
    padding: 8px 12px;
    font-family: monospace; }
  &__inner {
      padding: 10px 12px; } }


.post {
